.profile-input {
  width: 100%;
  background-color: #353535;
  border: 0.5px solid white;
  border-radius: 10px;
  padding: 10px;
  color: #ffffff;
  font-size: 16px;
  margin: 0;
}

.profile-image-buttons {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  gap: 10px; /*    */
}

.profile-image-button {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 14px;
  color: white;
  text-align: center;
  width: 50%;
  background-color: #1a58e0;
}

.profile-image-button.delete {
  background-color: #d32f2f;
}

.profile-image-button#file-upload {
  background-color: #1a58e0;
  display: flex;
  justify-content: center;
  align-items: center;
}

#file-upload {
  display: none;
}

.profile-save-container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto 10 auto;
  text-align: center;
}

.profile-save-button {
  padding: 15px 60px;
  border: none;
  background-color: #1a58e0;
  color: white;
  border-radius: 8px;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  transition: transform-color 0.3s;
}

.profile-save-button:hover {
  background-color: #3f71dd;
}

.password-change-section {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: center;
}

.profile-input-password {
  width: 100%;
  align-items: center;
  background-color: #353535;
  border: 0.5px solid white;
  border-radius: 10px;
  padding: 10px;
  color: #ffffff;
  font-size: 16px;
  margin: 0;
}

.profilepasswordToggleButton {
  position: absolute;
  right: 890px;
  transform: translateY(10%);
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.25rem;
}

.input-with-icon {
  position: relative; /*  */
}

.profile-input-password {
  width: 100%;
  /*      */
  padding-right: 42px;
}

.profilepasswordToggleButton {
  position: absolute;
  top: 50%;
  right: 10px; /*    */
  transform: translateY(-50%);
  width: 28px;
  height: 28px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 0;
  cursor: pointer;
}

.profilepasswordToggleButton:focus {
  outline: none;
}
.profilepasswordToggleButton img {
  width: 18px;
  height: 18px;
}

/*
 * ProfileModify.css
