 */

/* ProfileMain.css   react-select  '-mod'   */

/* 1. Control  (  )      */
.profile-form-group .nationality-mod__control,
.profile-form-group .position-select-mod__control {
  /* Control  select    */
  background-color: transparent !important;
  border: 1px solid white !important;
  border-radius: 10px !important;
  color: #ffffff !important;
  font-size: 16px !important;
  /*       */
  min-height: 40px !important;
  height: 40px !important; /*    */
  cursor: pointer !important;

  /*   */
  background-image: url('data:image/svg+xml;charset=UTF-8,%3csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"%3e%3cpolyline points="6 9 12 15 18 9"%3e%3c/polyline%3e%3c/svg%3e');
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 16px;

  box-shadow: none !important;
  padding: 0 !important;
}

/* 2. ValueContainer (   /  ) */
.profile-form-group .nationality-mod__value-container,
.profile-form-group .position-select-mod__value-container {
  padding: 0 0 0 10px !important; /*    */
  height: 100%; /* Control   */
  align-items: center; /*    */
}

/* 3. Indicators Container ( ) */
.profile-form-group .nationality-mod__indicators,
.profile-form-group .position-select-mod__indicators {
  height: 100%;
  align-items: center;
}

/* 4.    */
.profile-input.mt-2 {
  margin-top: 8px;
}

/*    */
.password-change-section {
  display: flex;
