  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  width: 28px;
  height: 28px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: transparent;
  border: 0;
  cursor: pointer;
}

.profilepasswordToggleButton:focus {
  outline: none;
}
.profilepasswordToggleButton img {
  width: 18px;
  height: 18px;
}

/* 5.   (Static)    (profile-input   ) */
.profile-input.static-info {
  /*  input   */
  background-color: transparent;
  border: 1px solid white; /* 0.5px -> 1px   (  0.5px   ) */
  border-radius: 10px;
  padding: 10px;
  color: #ffffff;
  font-size: 16px;
  /* **  :** (10px)     */
  min-height: 40px;
  height: auto; /*       */
  display: flex;
  align-items: center; /*        */
}

/* 6.       */
.profile-team-display.profile-input.static-info {
  gap: 8px; /*     */
}

/*    */
.profile-team-display.profile-input.static-info .profile-team-icon {
  width: 24px;
  height: 24px;
  object-fit: contain;
}
