/* :     */
.magicPencilOverlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.9);
  z-index: 10000;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
}

/*  :    */
.mpCloseBtn {
  position: absolute;
  top: 20px;
  right: 20px;
  background: #ff4444;
  color: white;
  border: none;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  cursor: pointer;
  font-size: 18px;
  z-index: 10001;
}

/* :      */
.magicPencilContainer {
  display: flex;
  flex-direction: row;
  gap: 20px;
  width: 100%;
  height: 100%;
  align-items: center;
  justify-content: center;
}

/*  :    ( ) */
.magicPencilTools {
  background: rgba(30, 30, 30, 0.85); /*     */
  padding-top: 10px;
  padding-bottom: 10px;
  border-radius: 12px;
  display: flex;
  flex-direction: column; /*    */
  align-items: center;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3);
  width: 90px;
  height: fit-content;
  max-height: 95%;
  overflow-y: auto;
  border: 1px solid #444;
}

/* :   */
.magicPencilCanvas {
  display: block;
  cursor: crosshair;
  border-radius: 8px;
  max-width: calc(100% - 100px); /*      */
  max-height: 100%;
  object-fit: contain;
}

/*  MODIFIED:       ( )  */

/*      */
.mpToolGroup,
.mpColorGroup,
.mpSizeGroup,
.mpEraserSize,
.mpActionGroup {
  display: flex;
  flex-direction: column; /*     */
  align-items: center; /*   */
  gap: 8px; /*     */
  width: 100%;
  padding: 15px 0;
  /* border-top: 1px solid #e0e0e0; ->   */
  border-top: 1px solid #555; /*      */
}

/*        */
.magicPencilTools > div:first-child {
  border-top: none;
  padding-top: 0;
}

/*      */
.magicPencilTools > div:last-child {
  padding-bottom: 0;
}

/*  END OF MODIFICATION  */

/* ,   ( ) */
.mpTool,
.mpActionGroup button {
  /* background: white; ->    */
  background: #444; /*     */
  /* border: 2px solid #ddd; ->    */
  border: 2px solid #666; /*    */
  color: #eee; /* /  */
  padding: 8px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s;
  width: 44px; /*    */
  height: 44px; /*    */
  display: flex;
  align-items: center;
  justify-content: center;
}

/* /    ( ) */
.mpTool.active,
.mpTool:hover {
  background: #007bff; /* /   */
  color: white;
  border-color: #007bff;
}

/*    ( ) */
.mpColor {
  width: 28px;
  height: 28px;
  border: 2px solid transparent;
  border-radius: 50%;
  cursor: pointer;
}

/*     ( ) */
.mpColor.active {
  /* border-color: #333; ->    */
  border-color: #fff; /*     */
  /* box-shadow: 0 0 0 2px white, 0 0 0 4px #333; ->   */
  box-shadow: 0 0 0 2px #333, 0 0 0 4px #fff; /*    */
}

/*     () */
.mpSizeGroup label {
  color: #eee;
}

/*  ( ) */
.mpSizeGroup input {
  width: 100px;
}

/*    ( ) */
.mpEraserSize-buttons button {
  padding: 6px 12px;
  /* background: white; ->    */
  background: #444; /*     */
  /* border: 2px solid #ddd; ->    */
  border: 2px solid #666; /*    */
  color: #eee; /*   */
  border-radius: 6px;
  cursor: pointer;
}

/*     ( ) */
.mpEraserSize-buttons button.active {
  background: #007bff; /*    */
  color: white;
  border-color: #007bff;
}

/*   ( ) */
.mpSaveBtn {
  background: #00c851 !important;
  color: white !important;
  border-color: #00c851 !important;
}

/*   ( ) */
.magicPencilCanvasContainer {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  pointer-events: auto;
}

/*     ( ) */
.magicPencilToolsContainer {
  position: fixed;
  left: 0;
  z-index: 1000;
  align-self: center;
  background-color: rgba(26, 29, 33, 0.95); /*    */
  backdrop-filter: blur(10px);
  border-left: 1px solid #333;
  display: flex;
  flex-direction: column;
  box-shadow: -5px 0 15px rgba(0, 0, 0, 0.3);
  transition: transform 0.3s ease-in-out;
  border-radius: 10px;
}

/*    (X)  */
/* .magicPencilToolsContainer       */
.mpCloseBtnTopRight {
  position: absolute;
  top: -12px; /*     */
  right: -12px; /*      */
  width: 28px;
  height: 28px;
  border-radius: 50%;
  background-color: #f44336; /*  */
  color: white;
  border: 1px solid white;
  font-size: 16px;
  font-weight: bold;
  cursor: pointer;
  z-index: 10; /*   */
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4);
  transition: all 0.2s ease;
}

.mpCloseBtnTopRight:hover {
  background-color: #d32f2f;
  transform: scale(1.1);
}

/* history button disabled  () */
.mpActionGroup button:disabled {
  background: #333;
  color: #777;
  cursor: not-allowed;
}


.magicPencilCanvasContainer.cursor-pencil {
  cursor: crosshair;
}
.magicPencilCanvasContainer.cursor-highlighter {
  cursor: crosshair;
}
.magicPencilCanvasContainer.cursor-select {
  cursor: grab;
}
