.profileForm {
    max-width: 800px;
    margin: 40px auto;
    padding: 20px;
    background-color: #444444;
    color: #fff;
    border-radius: 10px;
    border: 2px solid white;
}

.profileformtab-container {
    display: flex;
    width: 100%;
    margin-bottom: 15px;
}

.profileformTitle {
    flex: 1;
    padding: 5px;
    background-color: transparent;
    border: none;
    border-bottom: 3px solid #f77705;
    font-size: 25px;
    font-weight: 500;
    color: #ffffff;
    cursor: pointer;
    text-align: center;
}

.profileformSection {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-bottom: 30px;
}

.profileformimagePlaceholder {
    width: 250px;
    height: 300px;
    background-color: #C2C2C2;
    border-radius: 10px;
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: cover;
}

.profileformimagePlaceholder .profileImage {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px;
}

.profileformimageButtons {
    display: flex;
    gap: 30px;
}

.profileformuploadButton,
.profileformremoveButton {
    padding: 8px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    font-size: 14px;
    font-weight: bold;
}

.profileformuploadButton {
    background-color: #1A58E0;
    color: white;
}

.profileformremoveButton {
    background-color: #C2C2C2;
    color: black;
}

.profileformGrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;
}

.profileformGroup {
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.profileformGroup.full-width {
    grid-column: 1 / 3;
}

.profileformGroup label {
    font-size: 15px;
    font-weight: 400;
    color: #ffffff;
}

.profileformGroup input {
    background-color: transparent;
    border: 1px solid white;
    border-radius: 10px;
    padding: 10px;
    color: #ffffff;
    font-size: 16px;
}

.profileformGroup select, .profileformGroup Select {
  appearance: none;
  background-color: transparent;
  border: 1px solid white;
  border-radius: 10px;
  padding: 10px 40px 10px 10px;
  color: #ffffff;
  font-size: 16px;
  cursor: pointer;

  background-image: url('data:image/svg+xml;charset=UTF-8,%3csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"%3e%3cpolyline points="6 9 12 15 18 9"%3e%3c/polyline%3e%3c/svg%3e');
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 16px;
}

.profileformGroup select option {
  background-color: white;
  color: black;
}

.profileformGroup .input-with-button {
    display: flex;
    gap: 5px;
}

.profileformGroup .input-with-button input {
    flex-grow: 1;
}

.profileformGroup .input-with-button button {
    padding: 10px 15px;
    background-color: #1A58E0;
    color: white;
    font-weight: bold;
    border: none;
    border-radius: 10px;
    cursor: pointer;
}

.profileformsubmitButton {
    width: 100%;
    padding: 15px;
    background-color: #f77705;
    color: white;
    font-size: 18px;
    font-weight: bold;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    margin-top: 20px;
}

.profileform-team {
    position: relative;
}

.profileform-team-select {
    background-color: transparent;
    border: 1px solid white;
    border-radius: 10px;
    padding: 10px;
    color: #ffffff;
    font-size: 20px;
        display: flex;
    flex-direction: column;
    align-items: center;


    background-image: url('data:image/svg+xml;charset=UTF-8,%3csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"%3e%3cpolyline points="6 9 12 15 18 9"%3e%3c/polyline%3e%3c/svg%3e');
    background-repeat: no-repeat;
    background-position: right 10px center;
    background-size: 16px;
}

.profileform-team-select.placeholder {
    color: #b8b8b8;
}

.profileform-team-options {
    position: absolute;
    left: 0;
    right: 0;
    z-index: 10;
    background-color: #444444;
    border: 1px solid #616161;
    border-radius: 10px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    overflow-y: auto;    
}

.profileform-team-option {
    display: flex;
    align-items: center;
    padding: 10px;
    color: #ffffff;
    cursor: pointer;
}

.profileform-team-option:hover {
    background-color: #616161;
}

.profileform-team-icon {
    width: 200px;
    height: auto;
}

.profileform-team-no-options {
    padding: 10px;
    color: #b8b8b8;
    text-align: center;
}
/*
