/* 1. Control  (  ) */
.profileformGroup .nationality__control {
  /* Control  select      */
  background-color: transparent !important;
  border: 1px solid white !important;
  border-radius: 10px !important;
  padding: 0; /* Padding SingleValue/Placeholder  */
  color: #ffffff !important;
  font-size: 16px !important;
  cursor: pointer !important;

  /* select       */
  background-image: url('data:image/svg+xml;charset=UTF-8,%3csvg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" fill="none" stroke="white" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"%3e%3cpolyline points="6 9 12 15 18 9"%3e%3c/polyline%3e%3c/svg%3e');
  background-repeat: no-repeat;
  background-position: right 10px center;
  background-size: 16px;
  min-height: 40px; /*    ( 10px ) */
}

/*     */
.profileformGroup .nationality__control--is-focused {
  border-color: #f77705 !important;
  box-shadow: none !important;
}

/* 2. Menu  ( ) */
.profileformGroup .nationality__menu {
  /* styles prop ,      */
  background-color: #444444;
  border: 1px solid white;
  border-radius: 10px;
  margin-top: 5px; /* Control  */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

/* 3. Option  ( ) */
.profileformGroup .nationality__option {
  /*   */
  color: #ffffff;
  /* styles prop  */
}

/* /  */
.profileformGroup .nationality__option--is-focused {
  background-color: #616161 !important;
}

/*   */
.profileformGroup .nationality__option--is-selected {
  background-color: #f77705 !important;
  color: white !important;
}

/* 4. ValueContainer (   ) */
.profileformGroup .nationality__value-container {
  padding: 0; /*    (SingleValue/Placeholder ) */
}

