/* ===============================
   Notification Hover Icon (MVP)
     
   =============================== */

:root {
  /*       */
  --nh-bg: #1c1d21;
  --nh-bg-header: #1e2026;
  --nh-text: #e5e7eb;
  --nh-subtext: #9aa0a6;
  --nh-border: #2a2c33;
  --nh-accent: #5b9cff;   /* /  */
  --nh-hover: #23252c;
  --nh-shadow: rgba(0,0,0,0.45);
}

.nh-wrap {
  position: relative;
  display: inline-flex;
}

/*   */
.nh-bell {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 3.444rem;
  height: 3.444rem;
  border: 1px solid var(--nh-border);
  border-radius: 50%;
  background: #545454;
  cursor: pointer;
  color: #DEDEDE;
  transition: transform .12s ease, background-color .12s ease, border-color .12s ease;
}
.nh-bell:hover { background: var(--nh-hover); border-color: #353842; }
.nh-bell:active { transform: scale(0.96); }

/*   */
.nh-badge {
  position: absolute;
  top: -4px;
  right: -4px;
  min-width: 18px;
  height: 18px;
  padding: 0 6px;
  font-size: 12px;
  line-height: 18px;
  border-radius: 999px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background: var(--nh-accent);
  color: #0b1222; 
  font-weight: 700;
  box-shadow: 0 2px 6px var(--nh-shadow);
}

/*   */
.nh-panel {
  position: absolute;
  right: 0;
  top: calc(100% + 8px);
  width: 360px;
  max-height: 70vh;
  display: flex;
  flex-direction: column;
  overflow: hidden; /*   +   */
  border: 1px solid var(--nh-border);
  border-radius: 12px;
  background: var(--nh-bg);
  color: var(--nh-text);
  box-shadow: 0 16px 40px var(--nh-shadow);
  z-index: 1000; /*   */
  animation: nh-pop .12s ease;
}
@keyframes nh-pop {
  from { opacity: 0; transform: translateY(-4px) scale(0.98); }
  to   { opacity: 1; transform: translateY(0)    scale(1); }
}

/*   */
.nh-header {
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 12px;
  background: var(--nh-bg-header);
  border-bottom: 1px solid var(--nh-border);
}
.nh-grow { flex: 1; }
.nh-readall {
  font-size: 12px;
  padding: 6px 10px;
  border-radius: 8px;
  border: 1px solid var(--nh-border);
  background: transparent;
  color: var(--nh-text);
  cursor: pointer;
  transition: background-color .12s ease, border-color .12s ease, opacity .12s ease;
}
.nh-readall:hover { background: var(--nh-hover); border-color: #353842; }
.nh-readall:disabled { opacity: .5; cursor: default; }

/* ( ) */
.nh-list {
  overflow: auto;
  overscroll-behavior: contain;
  max-height: 70vh; /*  */
}
.nh-empty {
  padding: 20px 16px;
  color: var(--nh-subtext);
}

/*  */
.nh-item {
  width: 100%;
  text-align: left;
  padding: 10px 12px;
  border: 0;
  background: transparent;
  color: var(--nh-text);
  cursor: pointer;
  transition: background-color .12s ease, opacity .12s ease;
}
.nh-item:hover { background: var(--nh-hover); }
.nh-item.unread { font-weight: 600; }
.nh-item.read { opacity: .78; }

.nh-top {
  display: flex;
  align-items: center;
  gap: 6px;
  font-size: 12px;
  color: var(--nh-subtext);
}
.nh-type { font-weight: 600; }
.nh-dot {
  width: 8px;
  height: 8px;
  border-radius: 999px;
  background: var(--nh-accent);
}

.nh-msg {
  margin-top: 4px;
  font-size: 14px;
  line-height: 1.35;
  word-break: keep-all;
}
.nh-meta {
  margin-top: 4px;
  font-size: 12px;
  color: var(--nh-subtext);
}

/* () */
.nh-list::-webkit-scrollbar { width: 10px; }
.nh-list::-webkit-scrollbar-thumb {
  background: #2f3340;
  border-radius: 10px;
  border: 2px solid transparent;
  background-clip: padding-box;
}
.nh-list::-webkit-scrollbar-track { background: transparent; }

/* :    */
@media (prefers-reduced-motion: reduce) {
  .nh-bell { transition: none; }
  .nh-panel { animation: none; }
}
